<template>
  <div
    style="overflow:auto;height:460px;display: flex;flex-direction: column;justify-content: center;align-items: center;">
    <navMain :list="list" @del="del" />
    <navFooter :list="list" @clear="clear" />
    <navHeader @add="add" style="margin-left:5%" />

  </div>
</template>
<script>
import { defineComponent, computed, ref } from 'vue';
import { useStore } from 'vuex'
import navHeader from '@/components/navHeader'
import navMain from '@/components/navMain'
import navFooter from '@/components/navFooter'
export default defineComponent({
  name: 'Home',
  props: {

  },
  components: {
    navHeader,
    navMain,
    navFooter
  },
  setup (props, ctx) {
    let store = useStore()
    let list = computed(() => {
      return store.state.list
    })
    let value = ref('')
    let add = (val) => {
      value.value = val
      let flag = true
      list.value.map((item) => {
        if (item.title === value.value) {
          flag = false
          alert('任务已存在')
        }
      })
      // 没重复
      if (flag) {
        store.commit('addTodo', {
          title: value.value,
          complete: false
        })
      }
    }
    let del = (val) => {
      store.commit('delTodo', val)
    }
    // 清除已完成
    let clear = (val) => {
      store.commit('clear', val)
    }
    return {
      add,
      del,
      value,
      list,
      clear,
      del,
    }
  }
})
</script>

