<template>
    <div style="position: fixed;bottom: 5%;width: 300px;">
        <input placeholder="添加任务 按回车结束" v-model="value" @keydown.enter="enter"
            style="margin-right: 10%; background-color: #f6f6f6;height: 25px;border-color: transparent;">
        <button @click="changeBack" style="font-size:12px;padding:1%;">更换背景</button>
    </div>

</template>
<script>
import { defineComponent, ref } from 'vue'
export default defineComponent({
    name: 'navHeader',
    setup (props, ctx) {
        let value = ref('')
        let count = ref(0)
        let imgList = ref([
            require('../../assets/img/t1.jpg'),
            require('../../assets/img/t2.jpg'),
            require('../../assets/img/t3.jpg'),
            require('../../assets/img/t5.jpg'),
            require('../../assets/img/t6.jpg'),
            require('../../assets/img/t7.jpg'),
            require('../../assets/img/t8.jpg'),
            require('../../assets/img/t9.jpg'),
            require('../../assets/img/t10.jpg'),
        ])
        // 按回车确认
        let enter = () => {
            if (value.value) {
                ctx.emit('add', value.value)
                value.value = ''
            }
        }
        // 更换背景
        let changeBack = () => {
            if (count.value == 9) { count.value = 0 }
            var index = count.value++
            var imgs = imgList.value[index];
            console.log(document.body.style, 'document.body.style');
            document.body.style.backgroundImage = "url(" + imgs + ")"
            document.body.style.backgroundRepeat = "center no-repeat";
            document.body.style.backgroundSize = "cover";
        }
        return {
            imgList,
            count,
            value,
            enter,
            changeBack,
        }
    },

})
</script>
<style lang="scss" scoped>
input {
    margin-bottom: 10px;
}
</style>