<template>
    <div>
        child组件{{ msg }}

    </div>
    <button @click="toF">child组件</button>
</template>
<script>
import { defineComponent, ref } from 'vue'
export default defineComponent({
    props: {
        msg: {
            type: String
        }
    },
    setup (props, ctx) {
        let toF = () => {
            ctx.emit('send', 111)
        }
        return {
            toF
        }
    }
})
</script>
