<template>
    <div v-for="(item, index) in list" :key="index">
        <div class="item">
            <input type="checkbox" v-model="item.complete">
            <span style="color:#fff;font-weight: bold;">{{ item.title }}</span>

            <button class="del" @click="del(item, index)" style="margin-left:20px;">删除</button>
        </div>
    </div>
    <div v-if="list.length === 0" style="color:#fff">暂无任务</div>
</template>
<script>
import { defineComponent, ref } from 'vue'
export default defineComponent({
    name: 'navMain',
    props: {
        list: {
            type: Array,
            required: true
        }
    },
    emits: ['del'],
    setup (props, ctx) {
        // 删除任务
        let del = (item, index) => {
            ctx.emit('del', index)
        }
        return {
            del
        }
    }
})
</script>
<style lang="scss" scoped>
.item {
    height: 35px;
    width: 160px;
    line-height: 35px;
    position: relative;
    cursor: pointer;

    button {
        position: absolute;
        right: 20px;
        top: 6px;
        display: none;
        z-index: 99;
    }

    // 鼠标经过
    &:hover {
        background-color: #FF9966;
        border-radius: 2px;
        padding: 0 10px;

        button {
            display: block;
        }
    }
}
</style>